body {
    font-family: 'Arial', sans-serif;
    display: flex;
    background-image: url(https://images.pexels.com/photos/4577179/pexels-photo-4577179.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
    
   
  }
  
  .menu-page {
    max-width: 500px;
    display: flex;
    margin: 0 auto;
    padding: 20px;
    
    border-radius: 10px;
    
  }
  
  h1 {
    color:orange;
    text-align: center;
    margin-bottom: 20px;
    display: block;
  }
  
  .menu-category {
    margin-bottom:20 px;
    color: brown;
  }
  
  .category-heading {
    color: #f83d13;
    font-size: 24px;
    text-align: center;
    margin-bottom: 10px;
  }
  
  .menu-item {
    display: block;
    margin-bottom: 15px;
    background-image: url(https://images.pexels.com/photos/825661/pexels-photo-825661.jpeg?auto=compress&cs=tinysrgb&w=600);
    border: 1px solid #584c4c;
    border-radius: 5px;
    overflow: hidden;
    transition: transform 0.3s ease-in-out;
    backdrop-filter: blur(15px);
    background-image: url(https://images.pexels.com/photos/4577179/pexels-photo-4577179.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
  }
  
  .menu-item:hover {
    transform: scale(1.05);
  }
  
  .menu-item img {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 5px 0 0 5px;
  }
  
  .item-details {
    padding: 15px;
    display: inline-block;
  }
  
  .item-details h2 {
    color: #c41d1d;
    font-size: 20px;
    margin-bottom: 5px;
  }
  
  .item-details p {
    color: skyblue;
    margin-bottom: 10px;
  }
  
  .item-details button {
    background-color: green;
    color: bisque;
    border: none;
    padding: 8px 12px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out;
  }
  
  .item-details button:hover {
    background-color: rgb(34, 155, 203);
  }
  
  .order-summary {
    margin-top: 20px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    
  }
  
  .order-summary h2 {
    color: #333;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .order-summary ul {
    list-style: none;
    padding: 0;
  }
  
  .order-summary li {
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .order-summary li button {
    background-color: #e74c3c;
    color: #fff;
    border: none;
    padding: 5px 8px;
    cursor: pointer;
    border-radius: 3px;
    transition: background-color 0.3s ease-in-out;
  }
  
  .order-summary li button:hover {
    background-color: #c0392b;
  }
  
  .order-summary p {
    color: #777;
    margin-top: 10px;
    text-align: center;
  }
  
  .order-summary button {
    background-color: #3498db;
    color: #fff;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out;
  }
  
  .order-summary button:hover {
    background-color: #e80e15;
  }
  